import React, {Component} from 'react';

class ContactForm extends Component{
    constructor(props){
        super(props);

        this.state={
            email:'',
            name:'',
            msg:'',
        }
    }
    render(){
        return(
            <div className="container">
                <div className="row">
                    <div className="col-md-12">
                        <div className="form-wrapper">
                        <div className="kwes-form">
                            <form method="POST" action="https://kwes.io/api/foreign/forms/N6lHB6wNstIyIGYryz4G"
                            success-message="Great!, Will get back to you ASAP" 
                            error-message="Oops! Something went wrong, Try Again Please!" >
                                <div className="row">
                                    <div className="col-md-6">
                                        <div className="form-group">
                                            <label>Name</label>
                                            <input
                                                name="name"
                                                rules="required|max:255"
                                                type="text"
                                                className="form-control"
                                                placeholder="Name"
                                                value={this.state.name}
                                                onChange={e=>this.setState({name:e.target.value})}
                                            />
                                        </div>
                                    </div>
                                    <div className="col-md-6">
                                        <div className="form-group">
                                            <label>Email</label>
                                            <input
                                                name="email"
                                                rules="required|max:255"
                                                type="email"
                                                className="form-control"
                                                placeholder="Email"
                                                value={this.state.email}
                                                onChange={e=>this.setState({email:e.target.value})}
                                            />
                                        </div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-md-12">
                                        <div className="form-group">
                                            <label>Message</label>
                                            <textarea
                                                name="message"
                                                rules="required|max:500"
                                                type="text"
                                                className="form-control msg-body"
                                                placeholder="You'r Message for me"
                                                value={this.state.msg}
                                                onChange={e=>this.setState({msg:e.target.value})}
                                            />
                                        </div>
                                    </div>
                                </div>
                                <div className="text-center">
                                <button className="btn" type="submit">
                                    SUBMIT
                                </button>
                            </div>
                            </form>
                        </div>
                        </div>
                    </div>
                </div>
            </div>
            
        )
    }
}


export default ContactForm;